import React from 'react';
import { useLoaderData } from 'react-router-dom';
import {toast, ToastContainer } from 'react-toastify';
import './Quiz.css'
import 'react-toastify/dist/ReactToastify.css';

const Quiz = () => {
    const quiz = useLoaderData();
    const questions = quiz.data.questions;


    const show = (ans) => { 
        return toast.success(` Answer: ${ans}`, { autoClose: 1000 })
    }


    const answer = (opt, ans) => {
        if (opt === ans) {
            console.log("Conncet");
            return toast.success('Correct Answer!', { autoClose: 500 })
        }
        else {
            return toast.warning('Wrong Answer!', { autoClose: 500 })
        }

    }

    return (
        <div className='quiz'>
            {
                questions.map(question => {
                    return (

                        <div className='question-card'>
                            <div className="eye">
                                <svg onClick={() => show(question.correctAnswer)} xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth="1.5" stroke="currentColor" className="w-6 h-6">
                                    <path strokeLinecap="round" strokeLinejoin="round" d="M2.036 12.322a1.012 1.012 0 010-.639C3.423 7.51 7.36 4.5 12 4.5c4.638 0 8.573 3.007 9.963 7.178.07.207.07.431 0 .639C20.577 16.49 16.64 19.5 12 19.5c-4.638 0-8.573-3.007-9.963-7.178z" />
                                    <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                                </svg>

                            </div>
                            <h3>{question.question}</h3>
                            <div className='options'>
                                {
                                    question.options.map(option => {
                                        return (
                                            <div className='option'>
                                                <input onClick={() => answer(option, question.correctAnswer)} type="radio" name="option" id="" />
                                                <label htmlFor="">{option}</label>
                                            </div>
                                        )
                                    }
                                    )
                                }
                            </div>
                        </div>
                    )
                })




            }
            <ToastContainer />
        </div>
        
    );
};

export default Quiz;